import axios from 'axios';

let base = "http://121.36.39.135/api";

export const requestLogin = params => { return axios.post(`${base}/manager/login`,params).then(res => res.data); };

export const getUserList = params => { return axios.get(`${base}/user/allProduct`, { params: params }); };

export const getProductListPage = params => { return axios.post(`${base}/manager/product/search`,params); };

export const removeProduct = params => { return axios.get(`${base}/manager/product/removeProduct`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/manager/product/removeBatchProduct`, { params: params }); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const addUser = params => { return axios.get(`${base}/user/add`, { params: params }); };



export const getOrderListPage = params => { return axios.post(`${base}/manager/order/selectOrder`,params); };
export const setOrderDg = params => { return axios.post(`${base}/manager/order/orderDg`,params); };
export const setShipping = params => { return axios.post(`${base}/manager/shipping/add`,params); };
export const editOrderShipping = params => { return axios.post(`${base}/manager/order/editOrderShipping`,params); };

export const getCategory =params=> { return axios.post(`${base}/manager/category/deep/0`); };
export const addCategory =params=> { return axios.post(`${base}/manager/category/addCategory`,params); };


