<template>
    <div class="custom-tree-container">
        <div class="block">
            <el-tree
                    :data="categories"
                    node-key="id"
                    accordion
                    :expand-on-click-node="false"
                    :render-content="renderContent">
            </el-tree>
        </div>
    </div>
</template>

<script>
    let id = 1000;
    import { getCategory,addCategory } from '../../api/api';
    export default {
        name: "categoryManage",
        data() {
            const categories = [];
            return {
                categories: JSON.parse(JSON.stringify(categories)),
            }
        },
        methods: {
            append(data) {
                console.log(data)
                addCategory(params).then(res=>{

                });
                const newChild = { id: id++, label: 'testtest', children: [] };
                if (!data.children) {
                    this.$set(data, 'children', []);
                }
                data.children.push(newChild);
            },

            remove(node, data) {
                console.log(node)
                console.log(data)

                const parent = node.parent;
                const children = parent.data.children || parent.data;
                const index = children.findIndex(d => d.id === data.id);
                children.splice(index, 1);
            },

            renderContent(h, { node, data }) {
                return (
                    <span class="custom-tree-node">
                        <span>{node.label}</span>
                    <span>
                    <el-button size="mini" type="text" on-click={ () => this.append(data) }>编辑</el-button>
                <el-button size="mini" type="text" on-click={ () => this.append(data) }>新增</el-button>
                     <el-button size="mini" type="text" on-click={ () => this.remove(node, data) }>Delete</el-button>
                     </span>
                </span>);
            },
            getCategories(){
                var para;
                getCategory( para).then((res) => {
                    console.log(res.data.data[0].children)
                    this.categories=res.data.data[0].children
                })
            }
        },
        mounted(){
            this.getCategories()
        }

    }
</script>

<style>
    .custom-tree-node {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        padding-right: 8px;
    }
</style>


