<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="id"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getOrder">查询</el-button>
				</el-form-item>
				<!--<el-form-item>-->
					<!--<el-button type="primary" @click="handleAdd">新增</el-button>-->
				<!--</el-form-item>-->
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="order"  height="410" highlight-current-row v-loading="listLoading" style="width: 100%;" >
			<el-table-column type="expand">
				<!--:data="detailForm.orderItemVoList"-->
			<template  scope="scope">
				<el-table
						:data="scope.row.orderItemVoList"
						stripe
						style="width: 100%">
					<el-table-column
							prop="productId"
							label="商品id"
							width="100">
					</el-table-column>
					<el-table-column
							prop="productName"
							label="商品名称"
							width="180">
					</el-table-column>
					<el-table-column
							prop="currentUnitPrice"
							label="商品单价">
					</el-table-column>
					<el-table-column
							prop="quantity"
							label="商品数量">
					</el-table-column>
					<el-table-column
							prop="totalPrice"
							label="商品总价">
					</el-table-column>
				</el-table>
			</template>
			</el-table-column>
			<el-table-column prop="orderNo" width="130" label="订单编号" sortable>
			</el-table-column>
			<el-table-column prop="payment" label="总价" width="100" sortable>
			</el-table-column>
			<el-table-column prop="statusDesc" label="支付状态" min-width="100" sortable>
			</el-table-column>
			<el-table-column prop="receiverName" label="收货人" min-width="90" sortable>
			</el-table-column>
			<el-table-column prop="shippingVo.receiverMobile" label="手机" min-width="110" sortable>
			</el-table-column>
			<el-table-column prop="shippingVo.receiverProvince" label="省" min-width="80" sortable>
		   </el-table-column>
			<el-table-column prop="shippingVo.receiverCity" label="市" min-width="80" sortable>
			</el-table-column>
			<el-table-column prop="shippingVo.receiverDistrict" label="区/县" min-width="80" sortable>
			</el-table-column>
			<el-table-column prop="shippingVo.receiverAddress" label="详细地址" min-width="100" sortable>
			</el-table-column>
			<el-table-column prop="shippingVo.receiverZip" label="邮编" min-width="80" sortable>
			</el-table-column>
			<el-table-column prop="paymentTypeDesc" label="支付方式" width="100"  sortable>
			</el-table-column>
			<el-table-column prop="postage" label="邮费" min-width="80" sortable>
			</el-table-column>
			<el-table-column label="操作" width="150" fixed="right">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)" :disabled="scope.row.status!=10&&scope.row.status!=20">编辑</el-button>
					<el-button type="0" size="small" @click="handleDg(scope.$index, scope.row)" :disabled="scope.row.status!=20">发货</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>-->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="pages" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">

					<p>订单号{{editForm.orderNo}}</p>

                <p>总价￥{{editForm.payment}}</p>

				<el-form-item label="收货人">
					<el-input v-model="editForm.shippingVo.receiverName" auto-complete="off"></el-input>
				</el-form-item>
                <el-form-item label="手机">
                    <el-input v-model="editForm.shippingVo.receiverMobile" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="省">
                    <el-input v-model="editForm.shippingVo.receiverProvince" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="市">
                    <el-input v-model="editForm.shippingVo.receiverCity" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="区/县">
                    <el-input v-model="editForm.shippingVo.receiverDistrict" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="详细地址">
                    <el-input v-model="editForm.shippingVo.receiverAddress" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="邮编">
                    <el-input v-model="editForm.shippingVo.receiverZip" auto-complete="off"></el-input>
                </el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>



	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getOrderListPage,setOrderDg, setShipping, editOrderShipping } from '../../api/api';
    import Page4 from "../nav2/Page4";

	export default {
        components: {Page4},
        data() {
			return {

                orderStatus:40,
				imageHost:"http://img.cdn.imbession.top/",
				filters: {
					name: ''
				},
				order: [],
				pages: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列
				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
                    orderNo:0,
                    payment:0,
                    shippingVo:{
                        receiverName:"",
                        receiverMobile:"",
                        receiverProvince:"",
                        receiverCity:"",
                        receiverDistrict:"",
                        receiverAddress:"",
                        receiverZip:""
                    }
				},


			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getOrder();
			},
			//获取订单列表
			getOrder() {
				var para=new URLSearchParams
				para.append("pageNum",this.page)
				if(this.filters.name!=null)
					para.append("orderNo",this.filters.name);

				this.listLoading = true;
				//NProgress.start();
				getOrderListPage(para).then((res) => {
					console.log(res)
					this.pages = res.data.data.pages;
					this.order = res.data.data.list;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//发货
			handleDg: function (index, row) {

						this.$confirm('确认发货吗？', '提示', {
							confirmButtonText: '确定',
							cancelButtonText: '取消',
							type: 'warning',
							center: true
						}).then(() => {
							//NProgress.start();
							var para=new URLSearchParams
                            para.append("orderNo",row.orderNo)
							para.append("status",this.orderStatus)


							setOrderDg(para).then((res) => {

								if (res.data.data=="success"){
									this.$message({
										message: '发货成功',
										type: 'success'
									});
								} else {
									this.$message.error('发货失败');
								}
								 this.getOrder()
								//NProgress.done();
							});
						}).catch(() => {
							this.$message({
								type: 'info',
								message: '已取消发货'
							});
						});


			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},

			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							// let para = Object.assign({}, this.editForm);
                            var para=new URLSearchParams
                            para.append("receiverName",this.editForm.shippingVo.receiverName)
                            para.append("receiverMobile",this.editForm.shippingVo.receiverMobile)
                            para.append("receiverProvince",this.editForm.shippingVo.receiverProvince)
                            para.append("receiverCity",this.editForm.shippingVo.receiverCity)
                            para.append("receiverDistrict",this.editForm.shippingVo.receiverDistrict)
                            para.append("receiverAddress",this.editForm.shippingVo.receiverAddress)
                            para.append("receiverZip",this.editForm.shippingVo.receiverZip)
                            setShipping(para).then((res) => {
                                if (res.data.data!=0) {
                                    this.editLoading = false;
                                    var para1=new URLSearchParams
                                    para1.append("orderNo",this.editForm.orderNo)
                                    para1.append("shippingId",res.data.data)
                                    editOrderShipping(para1).then((res)=>{
                                        this.editLoading = false;
                                        if (res.data.data=="success"){
                                            this.$message({
                                                message: '提交成功',
                                                type: 'success'
                                            });
                                            this.getOrder();
                                        } else {
                                            this.$message.error('修改失败');
                                        }
                                    })
                                }else {
                                    this.$message.error('修改失败');
                                }
							});
						});
					}
				});
			},




		},
		mounted() {
			this.getOrder();
		}
	}

</script>

<style scoped>

</style>


